os empresas o duopolio natural ocurre cuando la cantidad demandada la pueden proporcionar solo dos empresas, no hay lugar para tres; sin embargo, si hubiera solo una empresa, esta obtendría utilidades económicas, por lo que entraría una segunda empresa para apropiarse de una parte del negocio y de sus utilidades económicas. Cuando un pequeño número de empresas comparte un mercado pueden aumentar sus utilidades al integrar un cártel y actuar como un monopolio. Un cártel es un conjunto de empresas que llega a un acuerdo de colusión para restringir la producción, aumentar los precios y las utilidades económicas, aunque los cárteles son ilegales, a veces operan en algunos mercados; sin embargo, por algunas razones que descubrirá en este capítulo, los cárteles tienden a desintegrarse.")])],1),_vm._m(8)]),_c('Separador'),_vm._m(9),_vm._m(10),_c('p',{staticClass:"mb-4"},[_vm._v("Best (2007) destaca que las estrategias basadas en el mercado se apoyan en una ventaja competitiva de conocimiento, tanto de clientes como de la competencia, se pueden ejecutar evitando confrontaciones de forma que se minimicen las pérdidas. A continuación, explica dos tipos de estrategias competitivas:")]),_vm._m(11),_vm._m(12),_vm._m(13),_c('Separador'),_vm._m(14),_vm._m(15),_vm._m(16),_c('h3',[_vm._v("Etapas en el proceso de benchmarking")]),_c('p',{staticClass:"mb-4"},[_vm._v("En el siguiente gráfico puede analizar en qué consisten las etapas del proceso de benchmarking.")]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-8"},[_c('ImagenInfografica',{staticClass:"color-secundario mb-5",scopedSlots:_vm._u([{key:"imagen",fn:function(){return [_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/proceso-benchmarking.svg"),"alt":"Proceso Benchmarking"}})])]},proxy:true}])},[_c('div',{staticClass:"tarjeta color-acento-botones p-4",attrs:{"x":"50%","y":"6.5%"}},[_c('div',{staticClass:"h4 mb-2"},[_vm._v("Determinar a qué se le va a hacer benchmarking")]),_c('p